import React, { Component } from 'react'
import Header from '../../components/header/Header'
import Footer from '../../components/footer/Footer'
import { useContext } from 'react'
import './shoppingCart.styles.css'
import { DishContext } from '../../App'

function ShoppingCart() {

    const dishData = useContext(DishContext);
    const [name, price, link] = dishData;
    console.log(name);

    return (
        
        <>
            <Header/>
                <div className="cart__title">Your Cart</div>
                <div className="item-container">

                    <div className="item">
                        <img src="" alt="Image" className="item-thumb" />
                        <div className="item-name">Some name 1</div>
                        <input type="number" className="item-quantity" />
                        <div className="item-total-price">$14</div>
                    </div>

                    <div className="item">
                        <img src="" alt="Image" className="item-thumb" />
                        <div className="item-name">Some name 2</div>
                        <input type="number" className="item-quantity" />
                        <div className="item-total-price">$14</div>
                    </div>

                </div>
            <Footer/>
        </>
        
    )
}

export default ShoppingCart




