import React, { Component } from 'react'
import { Route } from 'react-router-dom'
import Homepage from './pages/homepage/Homepage';
import ShoppingCart from './pages/shopping-cart/ShoppingCart';
import './App.css'

class App extends Component {

  constructor() {
    super();

    this.state = {
      dishData: ''
    }
  }

  dishDataCallback = (name, price, link) => {
    // console.log('Callback', name, price, link)
    return [name, price, link]
  }


  render() {

    return (
      <div className="App">
        <DishContext.Provider value={this.dishDataCallback}>
          <Route exact path="/Gourmet-World/" component={Homepage} />
          <Route exact path="/Gourmet-World/shopping-cart" component={ShoppingCart} />
        </DishContext.Provider>
      </div>
    )
  }
}

export default App;
export const DishContext = React.createContext();
// export const DishContextData = React.createContext();
